<template>
  <div id="navheader">
    <v-toolbar>
      <!-- 로그인 -->
      <!-- MY블로그없을때 -->
      <div v-if="userInfo" id="login">
        <!-- 메인로고 -->
        <router-link to="/main"><img id="logo"  :src="logo2" alt=""></router-link>
        
        <div v-if="userInfo.blogNo==0" @click="blogcreate">
          <b-button variant="link" style="text-decoration: none;">
            <router-link to="/blogcreate" style="text-decoration: none; color:black">블로그 생성</router-link>
          </b-button>
        </div>
        <!-- MY블로그있을때 -->
        <div v-if="userInfo.blogNo!=0" @click="myblog">
          <b-button variant="link" style="text-decoration: none;">
            <router-link :to="{path: `/bloghome/${userInfo.userId}`}" style="text-decoration: none; color:black"><v-icon large color="blue lighten-1">mdi-home</v-icon>My 블로그</router-link>
          </b-button>
        </div>

        <!-- 랭킹 -->
        <div class="my-3">
          <b-button id="popover-reactive-1" variant="link" style="text-decoration: none; color:black">
            <!-- <router-link to="/rank" style="text-decoration: none; color:black">랭킹</router-link> -->
            <v-icon large color="yellow darken-2">mdi-trophy</v-icon>
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-1"
          triggers="hover"
          placement="bottom"
        >
        <template v-slot:title>
          <!-- 진짜코드 -->
          <!-- <div @click="goblog(userInfo.userId)"><v-avatar><img :src="serverURL + userInfo.profileImg"></v-avatar> {{userInfo.nickName}}님은 현재 {{userInfo.userLevel}}입니다</div> -->
          <!-- 발표용코드 -->
          <div @click="goblog(userInfo.userId)"><v-avatar><img :src="serverURL + userInfo.profileImg"></v-avatar> {{userInfo.nickName}}님은 현재 4ZIT입니다</div>
        </template>
          <v-btn text><router-link to="/rank" style="text-decoration: none; color:black">랭킹보기</router-link></v-btn>
          <v-btn text @click="showPopup">계급표</v-btn>
        </b-popover>
        <!-- 계급표  -->

        <!-- 이웃보기 -->
        <div class="my-3">
          <b-button id="popover-reactive-2" variant="link" style="text-decoration: none; color:black">
            <v-icon large color="deep-purple lighten-3">mdi-account-multiple</v-icon>
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-2"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            <div v-for="(friend,index) in following" :key="friend.userNo">
              <button cols="8" @click="goblog(friend.userId)"><div v-if="index%2==0"><v-avatar><img :src="userImage[index]"></v-avatar>{{friend.nickName}}({{friend.userId}})</div></button>
              <button cols="8" @click="goblog(friend.userId)"><div v-if="index%2==1"><v-avatar><img :src="userImage[index]"></v-avatar>{{friend.nickName}}({{friend.userId}})</div></button>
              <br>
            </div>
          </template>
          <router-link to="/friends" style="text-decoration: none; color:black">이웃목록</router-link>
        </b-popover>
        

        
        <!-- 즐겨찾기 -->
        <div class="my-3">
          <b-button id="popover-reactive-3" variant="link" style="text-decoration: none; color:black">
            <v-icon large color="teal darken-2">mdi-star</v-icon>
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-3"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            {{ }}게시글제목
            <br>
            {{ }}게시글제목
            <br>
            {{ }}게시글제목
          </template>
          <router-link to="/bookmark" style="text-decoration: none; color:black">목록보기</router-link>
        </b-popover>
        

        <!-- 알림 -->
        <div class="my-3">
          <b-button id="popover-reactive-5" variant="link" style="text-decoration: none; color:black">
            <v-icon large color="yellow darken-4">mdi-bell</v-icon>
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-5"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            {{ }}Groot님이 회원님을 팔로우했습니다 <br>
            {{ }}Hundred님이 나의 게시물에 댓글을 남겼습니다
          </template>
        </b-popover>
        
        <!-- 쪽지 -->
        <div @click="messages">
          <b-button variant="link" style="text-decoration: none;">
          <router-link to="/messages" style="text-decoration: none; color:black"><v-icon large color="blue darken-2">mdi-message-text</v-icon></router-link>
          </b-button>
        </div>

        <!-- 마이페이지 -->
        <div class="my-3">
          <b-button id="popover-reactive-4" variant="link" style="text-decoration: none; color:black">
            <v-icon large color="grey lighten-1">mdi-account-circle</v-icon>
            {{ userInfo.userName }} 님
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-4"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            <router-link to="/mypage" style="text-decoration: none; color:black;">
              <v-avatar>
                <img :src="serverURL + userInfo.profileImg">
              </v-avatar>
              <b> {{ userInfo.userName }}</b>님
            </router-link>
            <v-btn small rounded outlined color="info" @click="Logout" style="margin:0 0 0 10px;">로그아웃</v-btn>
          </template>
        </b-popover>


        <!-- 비로그인 -->
      </div>
      
      <div v-else id="non_login">
        <!-- 메인로고 -->
        <router-link id="logo" to="/main" style="text-decoration: none; color:black"><h1><b>SSAZIT</b></h1></router-link>
        <b-button variant="link" style="text-decoration: none;">
          <router-link to="/login" style="text-decoration: none; color:black">로그인</router-link>
        </b-button>
        <b-button variant="link" style="text-decoration: none;">
          <router-link to="/join" style="text-decoration: none; color:black">회원가입</router-link>
        </b-button>
      </div>
    </v-toolbar>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "NavHeader",
  data() {
    return {
      logo: require("../images/logo.png"),
      logo2: require("../images/logo2.png"),
      logo3: require("../images/logo3.png"),
      following : [],
      userImage : [],
      showRank : false,
      ranking: [
        { rank: "9ZIT", content1: "가입 후 막 활동을 시작하는 멤버", content2: "자동등업 : 경험치 0이상 40미만"},
        { rank: "8ZIT", content1: "SSAZIT 일반 멤버", content2: "자동등업 : 경험치 40이상 100미만"},
        { rank: "7ZIT", content1: "SSAZIT 성실 멤버", content2: "자동등업 : 경험치 100이상 200미만"},
        { rank: "6ZIT", content1: "SSAZIT 우수 멤버", content2: "자동등업 : 경험치 200이상 500미만"},
        { rank: "5ZIT", content1: "SSAZIT 특별 멤버", content2: "자동등업 : 경험치 500이상 1000미만"},
        { rank: "4ZIT", content1: "SSAZIT VIP 멤버", content2: "자동등업 : 경험치 1000이상 10000미만"},
        { rank: "SSAZIT", content1: "SSAZIT 매니저"},
      ]
    };
  },
  created() {
    axios.get('/user/following')
    .then((Response)=>{ 
      console.log(Response.data);
      this.following = Response.data;
      for(let i=0;i<Response.data.length;i++){
        Response.data[i].userId
        console.log(Response.data[i].userId+"이웃 아이디")
        axios.post('/user/list',{
        searchKey : "userId",
        searchValue : Response.data[i].userId
        }).then((Res)=>{
        console.log(Res.data);  
        console.log("Hi");

        for(let j=0;j<Res.data.length;j++){
          if(Res.data[j].userId==Response.data[i].userId){
            this.blogInfo = Res.data[j]; // 해당 블로그 정보
            this.userImage.splice(i,0,this.serverURL + this.blogInfo.profileImg);
            break;
          }
        }
        // this.blogInfo = Response.data[0];
        console.log(this.blogInfo)
        console.log("누구인가");
    })
      }
      
    })
  },
  computed: {
    userInfo() {
      return this.$store.getters.userInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  methods: {
    Logout() {
      this.$router.push('/logout');
    },
    blogcreate(){
      this.$router.push("/blogcreate");
    },
    myblog() {
      this.$router.push({path: `/bloghome/${this.userInfo.userId}`});
    },
    rank(){
      this.$router.push("/rank");
    },
    messages(){
      this.$router.push("/messages");
    },
    goblog(friendid){
      // this.$router.push('/bloghome/'+friendid);
      this.$router.push({ name: 'BlogBase', params: { userId: friendid, key: 'friend' }});
    },
    showPopup() { 
      window.open("http://127.0.0.1:5500/rank.html", "rank", "width=600, height=400, left=100, top=50"); 
    }
  }
};

</script>

<style scoped>
li {
  margin: 0 10px;
}
div {
  display: inline;
}
#logo {
  position: fixed;
  height: 50px;
  top: 10px;
  left: 100px;
}
#friend {
  position: fixed;
  top: 10px;
  left: 200px;
}
#login {
  position: fixed;
  top: 15px;
  right: 100px;
}
#non_login {
  position: fixed;
  top: 20px;
  right: 100px;
}
#right {
  float: right;
  width: 100%;
}
</style>