<template>
  <div>
    <div class="article-setting" style="height:100%; width:1050px;">
      <v-row class="pl-3 mb-6" style="justify-content:start; width:1050px;">        
        <h4 class="text-justify font-weight-bold m-0" style="vertical-align:middle;">
          <div style="height:10px;"></div>
          책만들기
        </h4>
        <v-spacer></v-spacer>
        <!-- 글삭제 버튼[수정시에만] -->
        <div class="my-2 mx-0" style="justify-content:end;">
          <!-- 누르면 경고창!! -->
          <!-- <v-btn class="pr-0" text color="error">글 삭제</v-btn> -->
        </div>
      </v-row>

      <div class="article-setting-base" style="width:1050px;">
      

      <div class="mt-5 mb-3" style="text-align:left;">
        <b>게시글 가져오기</b>
      </div>
      <v-row class="pl-0 mt-0 mr-0" justify="start" style="width:1050px;">

        <v-col class="tab-wrap px-0">

          <input type="radio" id="tab1" name="tabGroup1" class="tab" checked>
          <label for="tab1">목차</label>

          <input type="radio" id="tab2" name="tabGroup1" class="tab">
          <label for="tab2">내용</label>

          <input type="radio" id="tab3" name="tabGroup1" class="tab">
          <label for="tab3">출처</label>
          <div class="tab__content">
            <ckeditor :config="editorConfig"></ckeditor>
          </div>
          <div class="tab__content">
            <ckeditor :config="editorConfig"></ckeditor>
          </div>
          <div class="tab__content">
            <ckeditor :config="editorConfig"></ckeditor>
          </div>
        </v-col>
        <v-col cols="3" class="py-0 pr-0 mt-5">
          <div style="height:460px; overflow:scroll; border: 1px solid grey">
            <div style="border-bottom: 1px solid grey">전체글 목록</div>
            <div class="list-group-item" v-for="articleItem in articleItems" :key="articleItem.article.articleNo">
              <a style="color:black;">{{articleItem.article.title}}</a>
            </div>
          </div>
        </v-col>

      </v-row>

      <div class="mt-5 mb-3" style="text-align:left;">
        <b>태그 편집</b>
      </div>
      <v-col cols="11" class="p-0">
        <v-combobox style=" z-index:9999;"
          v-model="tagSelected"
          :items="tagItems"
          item-value="tagNo" 
          item-text="tagName"
          label="태그를 선택해주세요. (입력후 enter)"
          multiple
          chips
          class="p-0 m-0"
        ></v-combobox>
      </v-col>
      <v-row>
          <v-spacer></v-spacer>
          <v-btn-toggle
            borderless
            cols="1"
          >
            <v-btn :to="'/bloghome/' + $store.getters.userInfo.userId + '/bookcover'" @click="writeBook" value="작성" color="white" class="text-decoration-none">
              <span class="hidden-sm-and-down mr-1">이전</span>
              <v-icon small>mdi-pencil</v-icon>
            </v-btn>
            <v-btn @click="writeBook" value="작성" color="white" class="text-decoration-none">
              <span class="hidden-sm-and-down mr-1">작성</span>
              <v-icon small>mdi-pencil</v-icon>
            </v-btn>
          </v-btn-toggle>
          <v-col cols="1"></v-col>
        </v-row>
        <hr>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  components:{
  },
  data() {
    return {
      book:{
        bookTitle: '',
        openScope: '',
        version: '',
        categoryNo: '',
      },
      coverFront: this.$store.getters.serverURL + 'resource/book_img/default_front.JPEG',
      coverMiddle: this.$store.getters.serverURL + 'resource/book_img/default_middle.JPG',
      coverBack: this.$store.getters.serverURL + 'resource/book_img/default_back.JPG',
      front_img_file: '',
      middle_img_file: '',
      back_img_file: '',
      articleItems: [],
      articleSelected: [],
      Bcontent: [],
      openItems: [
        {
          value:'public',
          name:'전체공개'
        },
        {
          value:'protected',
          name:'맞팔공개'
        },
        {
          value:'private',
          name:'비공개'
        }
      ],
      categoryItems: [],
      catSelected: '',
      booktag: [],
      tmptag: [],
      tagItems: [],
      tagSelected: [],
      editorConfig: {
          extraPlugins: 'prism, youtube, image2',
          codeSnippet_theme: 'monokai_sublime',
          filebrowserImageBrowseUrl: 'http://localhost:9999/ckfinder/static/ckfinder.html',
        filebrowserImageUploadUrl: 'http://localhost:9999/ckfinder/connector?command=FileUpload&lang=ko&langCode=ko&type=Images&currentFolder=%2F&hash=64b554bde9ee58bd&responseType=json',
        filebrowserUploadUrl : 'http://localhost:9999/ckfinder/connector?command=FileUpload&lang=ko&langCode=ko&type=Images&currentFolder=%2F&hash=64b554bde9ee58bd&responseType=json'
      },
    }
  },
  computed:{
    userInfo(){
      return this.$store.getters.userInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  created(){
    // categoryItems
    axios.get(`/article/categorys`)
    .then((Response)=>{
      console.log(Response.data);
      this.categoryItems = Response.data;
    })
    .catch(()=>{
      alert("카테고리 목록을 불러오는 데 실패했습니다.")
    })

    // articleItems
    axios.get(`/article/articles?userNo=` + encodeURI(this.userInfo.userNo))
    .then((Response)=>{
      console.log(Response.data);
      this.articleItems = Response.data;
    })
    .catch(()=>{
      alert("게시글 목록을 불러오는 데 실패했습니다.")
    })

    axios.get(`/tag/tags`)
    .then((Response) => {
      this.tagItems = Response.data;
      console.dir(this.tagItems);
    })
    .catch(() => {
      alert("태그 목록을 불러오는 데 실패했습니다.")
    })
  },
  methods: {
    frontFileChange(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      console.log("filechange!");
      this.front_img_file = files[0];
      console.log(files[0]);
      this.createFrontImage(files[0]);
    },
    createFrontImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.coverFront = e.target.result;
      } 
      console.log("createImage!!");
      reader.readAsDataURL(file);
    },
    middleFileChange(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      console.log("filechange!");
      this.middle_img_file = files[0];
      console.log(files[0]);
      this.createMiddleImage(files[0]);
    },
    createMiddleImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.coverMiddle = e.target.result;
      } 
      reader.readAsDataURL(file);
    },
    backFileChange(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      this.back_img_file = files[0];
      console.log(files[0]);
      this.createBackImage(files[0]);
    },
    createBackImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.coverBack = e.target.result;
      } 
      reader.readAsDataURL(file);
    },
    log:function(evt){
      window.console.log(evt);
    },
    writeBook(){

      // 데이터 유효성 검사 ~
      // ~ 데이터 유효성 검사

      // 사전작업 ~
      console.log("-----사전작업-----");
        // 책
      console.log("책");
      this.book.categoryNo = this.catSelected.categoryNo;
      console.log(this.book);
      
        // 책 내용
      console.log("내용");
      for(let index=0; index<this.articleSelected.length; index++){
        this.Bcontent.push({
          seqNo : index+1,
          chapter : this.articleSelected[index].article.title,
          content : this.articleSelected[index].article.content,
          articleNo : this.articleSelected[index].article.articleNo
        })
      }
      console.log(this.Bcontent);

        // 태그
      console.log("태그");
      for(let tag of this.tagSelected){
        if(tag instanceof Object && tag.tagNo){
          this.booktag.push({tagNo : tag.tagNo});
        }else{
          this.tmptag.push({tmptagName : tag});
        }
      }
      console.log(this.booktag);
      console.log(this.tmptag);

      // ~ 사전작업

      // 데이터 전송 ~
      
      // 사진 전송
      var form = new FormData();
      form.append("frontFile",this.front_img_file);
      form.append("middleFile",this.middle_img_file);
      form.append("backFile",this.back_img_file);
      axios.post(`/file/create_book_cover`, form
      ).then((Response)=>{
        console.log(Response.data);
        // 책 전송
        axios.post(`/book/CUD`, {
          book: this.book,
          bcontent: this.Bcontent,
          booktag: this.booktag,
          tmptag: this.tmptag,
          fileinfo: Response.data
        }).then(()=>{
          alert("책 작성이 완료되었습니다.");
          this.$router.go(-1);
        }).catch(()=>{
          alert("책 작성을 실패했습니다.");
          this.Bcontent = [];
          this.BookTagCat = [];
          this.booktag = [];
          this.tmptag = [];
        })
      }).catch(()=>{
        alert("이미지 업로드에 실패했습니다.");
        this.Bcontent = [];
        this.BookTagCat = [];
        this.booktag = [];
        this.tmptag = [];
      })
      
      // ~ 데이터 전송

    }
  }
}
</script>


<style>
.tab-wrap {
  max-width: 100%;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  list-style: none;
  background-color: #fff;
  margin: 18px 0;
  padding: 0;
}
.tab {
  display: none;
}
.tab:checked:nth-of-type(1) ~ .tab__content:nth-of-type(1) {
  opacity: 1;
  -webkit-transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease, 0.8s -webkit-transform ease;
  position: relative;
  top: 0;
  z-index: 100;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  text-shadow: 0 0 0;
}
.tab:checked:nth-of-type(2) ~ .tab__content:nth-of-type(2) {
  opacity: 1;
  -webkit-transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease, 0.8s -webkit-transform ease;
  position: relative;
  top: 0;
  z-index: 100;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  text-shadow: 0 0 0;
}
.tab:checked:nth-of-type(3) ~ .tab__content:nth-of-type(3) {
  opacity: 1;
  -webkit-transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease, 0.8s -webkit-transform ease;
  position: relative;
  top: 0;
  z-index: 100;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  text-shadow: 0 0 0;
}
.tab:checked:nth-of-type(4) ~ .tab__content:nth-of-type(4) {
  opacity: 1;
  -webkit-transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease, 0.8s -webkit-transform ease;
  position: relative;
  top: 0;
  z-index: 100;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  text-shadow: 0 0 0;
}
.tab:checked:nth-of-type(5) ~ .tab__content:nth-of-type(5) {
  opacity: 1;
  -webkit-transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s -webkit-transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease;
  transition: 0.5s opacity ease-in, 0.8s transform ease, 0.8s -webkit-transform ease;
  position: relative;
  top: 0;
  z-index: 100;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  text-shadow: 0 0 0;
}
.tab:first-of-type:not(:last-of-type) + label {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.tab:not(:first-of-type):not(:last-of-type) + label {
  border-radius: 0;
}
.tab:last-of-type:not(:first-of-type) + label {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.tab:checked + label {
  background-color: #fff;
  box-shadow: 0 -1px 0 #fff inset;
  cursor: default;
}
.tab:checked + label:hover {
  box-shadow: 0 -1px 0 #fff inset;
  background-color: #fff;
}
.tab + label {
  box-shadow: 0 -1px 0 #eee inset;
  border-radius: 6px 6px 0 0;
  cursor: pointer;
  display: block;
  text-decoration: none;
  color: #333;
  -webkit-box-flex: 3;
          flex-grow: 3;
  text-align: center;
  background-color: #f2f2f2;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  -webkit-transition: 0.3s background-color ease, 0.3s box-shadow ease;
  transition: 0.3s background-color ease, 0.3s box-shadow ease;
  height: 50px;
  box-sizing: border-box;
  padding: 15px;
}
.tab + label:hover {
  background-color: #f9f9f9;
  box-shadow: 0 1px 0 #f4f4f4 inset;
}
.tab__content {
  background-color: transparent;
  position: absolute;
  width: 100%;
  z-index: -1;
  opacity: 0;
  left: 0;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  border-radius: 6px;
}

</style>