            </v-col>
          </v-row>
        </v-row>
        <hr>

        <div :class="{ hideArea : isImg }">
          <p align="left" class="m-0 font-weight-bold">표지 설정</p>
          <!-- <hr class="mt-0"> -->
          <!-- 드래그 업로드할꺼면 필요없는 부분 -->
          <!-- ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ -->
          <v-row class="px-5 mr-16">
            <v-col cols="1" align="left" class="p-0">
              <div style="height:13px;"></div>
              <p>앞 표지</p>
            </v-col>
            <v-col class="px-2 pt-0" align="left" style="width:80%; height:44px; border: 1px solid grey; border-radius: 5px;">
              <div style="height:6px;"></div>
              <p class="p-0 m-0" style="color:grey;">
                <input type="file" @change="frontFileChange" ref="front_image_upload"/>
              </p>
            </v-col>
            <div>
              <div style="height:5px;"></div>
              <v-btn icon value="삭제" color="white" class="text-decoration-none">
                <v-icon color="grey">mdi-close</v-icon>
              </v-btn>
            </div>

          </v-row>
          <v-row class="px-5 mr-16">
            <v-col cols="1" align="left" class="p-0">
              <div style="height:13px;"></div>
              <p>등 표지</p>
            </v-col>
            <v-col class="px-2 pt-0" align="left" style="width:80%; height:44px; border: 1px solid grey; border-radius: 5px;">
              <div style="height:6px;"></div>
              <p class="p-0 m-0" style="color:grey;"><input type="file" @change="middleFileChange" ref="middle_image_upload"/></p>
            </v-col>
            <div>
              <div style="height:5px;"></div>
              <v-btn icon value="삭제" color="white" class="text-decoration-none">
                <v-icon color="grey">mdi-close</v-icon>
              </v-btn>
            </div>
          </v-row>
          <v-row class="px-5 mr-16">
            <v-col cols="1" align="left" class="p-0">
              <div style="height:13px;"></div>
              <p>뒷 표지</p>
            </v-col>
            <v-col class="px-2 pt-0" align="left" style="width:80%; height:44px; border: 1px solid grey; border-radius: 5px;">
              <div style="height:6px;"></div>
              <p class="p-0 m-0" style="color:grey;"><input type="file" @change="backFileChange" ref="back_image_upload"/></p>
            </v-col>
            <div>
              <div style="height:5px;"></div>
              <v-btn icon value="삭제" color="white" class="text-decoration-none">
                <v-icon color="grey">mdi-close</v-icon>
              </v-btn>
            </div>
          </v-row>
          <br>

          <p align="left" class="m-0 font-weight-bold pb-3">미리보기</p>
          <!-- <hr class="mt-0"> -->
          <!-- ㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡㅡ -->

          <!-- 400x500? 110x700?-->
          <!-- 미리보기 -->
          <v-row class="px-4 mb-5" >
            <!-- 뒤 -->
            <v-col class="p-0" cols="5" style="border: 1px solid grey; height:545px; overflow:hidden;">
              <v-img :src="coverBack" height="545px" ></v-img>
            </v-col>
            <!-- 중 -->
            <v-col class="p-0" cols="1" style="border: 1px solid grey; height:545px; overflow:hidden;">
              <v-img :src="coverMiddle" height="545px"></v-img>
            </v-col>
            <!-- 앞 -->
            <v-col class="p-0" cols="5" style="border: 1px solid grey; height:545px; overflow:hidden;">
              <v-img :src="coverFront" height="545px"></v-img>
            </v-col>
            <div> 
              <div style="height:250px;"></div>
              <v-btn-toggle
                borderless
                cols="1"
              >
                <v-btn icon color="white" class="text-decoration-none" @click="toggle">
                  <v-icon x-large>mdi-chevron-right</v-icon>
                </v-btn>
              </v-btn-toggle>
            </div>
          </v-row>

        <hr>
        </div>

        <!-- 책 내용 영역 -->
        <div :class="{ hideArea : isContent }">
          <div class="mt-5 mb-3" style="text-align:left;">
            <b>게시글 가져오기</b>
          </div>
          <v-row class="pl-0 mt-0 mr-0" justify="start" style="width:1050px;">
            <div> 
              <div style="height:250px;"></div>
              <v-btn-toggle
                borderless
                cols="1"
              >
                <v-btn icon color="white" class="text-decoration-none" @click="toggle">
                  <v-icon x-large>mdi-chevron-left</v-icon>
                </v-btn>
              </v-btn-toggle>
            </div>
            <v-col class="tab-wrap px-0">

              <input type="radio" id="tab1" name="tabGroup1" class="tab" checked>
              <label for="tab1">목차</label>

              <input type="radio" id="tab2" name="tabGroup1" class="tab">
              <label for="tab2">내용</label>

              <input type="radio" id="tab3" name="tabGroup1" class="tab">
              <label for="tab3">출처</label>
              <div class="tab__content">
                <ckeditor :config="editorConfig" v-model="book.bookIndex"></ckeditor>
              </div>
              <div class="tab__content">
                <ckeditor :config="editorConfig" v-model="book.bookContent"></ckeditor>
              </div>
              <div class="tab__content">
                <ckeditor :config="editorConfig" v-model="book.bookReference"></ckeditor>
              </div>
            </v-col>
            <v-col cols="3" class="py-0 pr-0 mt-5">
              <div style="height:460px; overflow:scroll; border: 1px solid grey">
                <div style="border-bottom: 1px solid grey">전체글 목록</div>
                <div class="list-group-item" v-for="articleItem in articleItems" :key="articleItem.article.articleNo">
                  <a style="color:black;" @click="addContent(articleItem.article)">{{articleItem.article.title}}</a>
                </div>
              </div>
            </v-col>
          </v-row>
          <hr>
        </div>

        <!-- 태그 영역 -->
        <div class="mt-5 mb-3" style="text-align:left;">
          <b>태그 편집</b>
        </div>
        <v-col cols="11" class="p-0">
          <v-combobox style=" z-index:9999;"
            v-model="tagSelected"
            :items="tagItems"
            item-value="tagNo" 
            item-text="tagName"
            label="태그를 선택해주세요. (입력후 enter)"
            multiple
            chips
            class="p-0 m-0"
          ></v-combobox>
        </v-col>

        <v-row>
          <v-spacer></v-spacer>
            <v-btn-toggle
              borderless
              cols="1"
            >
              <v-btn value="작성" color="white" class="text-decoration-none" @click="writeBook">
                <span class="hidden-sm-and-down mr-1">작성</span>
                <v-icon small>mdi-pencil</v-icon>
              </v-btn>
            </v-btn-toggle>
          <v-col cols="1"></v-col>
        </v-row>
          <hr>

      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  components:{
  },
  data() {
    return {
      book:{
        writer: '',
        bookTitle: '',
        bookIndex: '<p>목차</p>\n\n<p>1. 챕터1</p>\n\n<p>2. 챕터2</p>\n\n<p>3. 챕터3</p>\n\n<p>4. 챕터4</p>\n\n<p>5. 챕터5</p>\n',
        bookContent: '',
        bookReference: '<p>출처</p>\n\n<p>(번호. 아이디.(게시글작성날짜).게시글명/챕터명)</p>\n\n<p>1. bjh7013.(2020.08.09).영주바보 그건맞지/chapter1</p>\n\n<p>&nbsp;</p>\n',
        openScope: '',
        version: '',
        categoryNo: '',
      },
      target:'',
      coverFront: this.$store.getters.serverURL + 'resource/book_img/default_front.JPEG',
      coverMiddle: this.$store.getters.serverURL + 'resource/book_img/default_middle.JPG',
      coverBack: this.$store.getters.serverURL + 'resource/book_img/default_back.JPG',
      front_img_file: '',
      middle_img_file: '',
      back_img_file: '',
      articleItems: [],
      // articleSelected: [],
      // Bcontent: [],
      openItems: [
        {
          value:'public',
          name:'전체공개'
        },
        {
          value:'protected',
          name:'맞팔공개'
        },
        {
          value:'private',
          name:'비공개'
        }
      ],
      categoryItems: [],
      catSelected: '',
      booktag: [],
      tmptag: [],
      tagItems: [],
      tagSelected: [],
      isImg: false,
      isContent: true,
      editorConfig: {
          extraPlugins: 'prism, youtube, image2',
          codeSnippet_theme: 'monokai_sublime',
          filebrowserImageBrowseUrl: 'http://localhost:9999/ckfinder/static/ckfinder.html',
        filebrowserImageUploadUrl: 'http://localhost:9999/ckfinder/connector?command=FileUpload&lang=ko&langCode=ko&type=Images&currentFolder=%2F&hash=64b554bde9ee58bd&responseType=json',
        filebrowserUploadUrl : 'http://localhost:9999/ckfinder/connector?command=FileUpload&lang=ko&langCode=ko&type=Images&currentFolder=%2F&hash=64b554bde9ee58bd&responseType=json'
      },
    }
  },
  computed:{
    userInfo(){
      return this.$store.getters.userInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  created(){
    // categoryItems
    axios.get(`/article/categorys`)
    .then((Response)=>{
      console.log(Response.data);
      this.categoryItems = Response.data;
    })
    .catch(()=>{
      alert("카테고리 목록을 불러오는 데 실패했습니다.")
    })

    // articleItems
    axios.get(`/article/articles?userNo=` + encodeURI(this.userInfo.userNo))
    .then((Response)=>{
      console.log(Response.data);
      this.articleItems = Response.data;
    })
    .catch(()=>{
      alert("게시글 목록을 불러오는 데 실패했습니다.")
    })

    axios.get(`/tag/tags`)
    .then((Response) => {
      this.tagItems = Response.data;
      console.dir(this.tagItems);
    })
    .catch(() => {
      alert("태그 목록을 불러오는 데 실패했습니다.")
    })
  },
  methods: {
    parse(){
      var sax = require("sax");
      let strict = false; // set to false for html-mode
      let parser = sax.parser(strict, {lowercase : true});
      let parsed = '';
      // let target = '';
      // console.log(this.book.bookContent)
      let isSelfClosing = false;
      let isPageBreak = false;

      parser.onerror = (e) => {
        // an error happened.
        alert('!!!!!!!!!!!!!!!!!!!!!!!!!')
        console.log(e);
      };

      parser.ontext =  (t) => {
        // got some text.  t is the string of text.
        console.log('ontext==========================================');
        console.log(t);
        parsed += t;
      };

      parser.onopentag = (node) => {
        // opened a tag.  node has "name" and "attributes"
        // alert('dddddddd')
        console.log('onopentag==========================================');
        console.dir(node);
        let tmp = "<" + node.name;
        console.log(tmp);
        console.log(parsed);
        
        if(node.name == 'root'){
          return;
        }

        if(isPageBreak || node.attributes.style == 'page-break-after:always'){
          // alert('pagebreak')
          isPageBreak = true;
          console.log(isPageBreak)
        }else{
          for(let attr of Object.keys(node.attributes)){
            tmp += ` ${attr}="${node.attributes[attr]}"`; 
          }
  
          if(node.isSelfClosing){
            tmp += "/>";
            isSelfClosing = true;
          }else{
            tmp += ">";
          }
  
          parsed += tmp;
        }
      };

      parser.onclosetag = (node) => {
        console.log('onclosetag==========================================');

        console.dir(node);
        if(node != 'root'){
          
          if(isPageBreak){ // page break인 div태그가 닫혔을 경우 해당 태그 제거 후 정해진 범위 page로 묶어주기
            if(node == 'div'){
             
              parsed = '<div class="page">' + parsed + '</div>'
              this.target += parsed;
              parsed = '';
              isPageBreak = false;
            }
          }else{
            if(!isSelfClosing){
              parsed += `</${node}>`
            }
  
            isSelfClosing = false;
          }
              
        }else{
          parsed = '<div class="page">' + parsed + '</div>'
          this.target += parsed;
          parsed = '';
        }

        
      };
     
      parser.onend = () => {
        // parser stream is done, and ready to have more stuff written to it.
        console.log('end==================================================')
        alert('end')
        // this.target += parsed;
      };
      
      parser.write('<root>' + this.book.bookContent + '</root>');
      
      console.log('11111111111111111111111111111111111111111111111111111111111111111')
      console.log(this.target);
    },
    frontFileChange(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      console.log("filechange!");
      this.front_img_file = files[0];
      console.log(files[0]);
      this.createFrontImage(files[0]);
    },
    createFrontImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.coverFront = e.target.result;
      } 
      console.log("createImage!!");
      reader.readAsDataURL(file);
    },
    middleFileChange(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      console.log("filechange!");
      this.middle_img_file = files[0];
      console.log(files[0]);
      this.createMiddleImage(files[0]);
    },
    createMiddleImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.coverMiddle = e.target.result;
      } 
      reader.readAsDataURL(file);
    },
    backFileChange(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      this.back_img_file = files[0];
      console.log(files[0]);
      this.createBackImage(files[0]);
    },
    createBackImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.coverBack = e.target.result;
      } 
      reader.readAsDataURL(file);
    },
    log:function(evt){
      window.console.log(evt);
    },
    writeBook(){

      // 데이터 유효성 검사 ~
      // ~ 데이터 유효성 검사

      // 사전작업 ~
      console.log("-----사전작업-----");
        // 책
      console.log("책");
      this.book.categoryNo = this.catSelected.categoryNo;
      console.log(this.book);
      
        // 책 내용
      // console.log("내용");
      // for(let index=0; index<this.articleSelected.length; index++){
      //   this.Bcontent.push({
      //     seqNo : index+1,
      //     chapter : this.articleSelected[index].article.title,
      //     content : this.articleSelected[index].article.content,
      //     articleNo : this.articleSelected[index].article.articleNo
      //   })
      // }
      // console.log(this.Bcontent);

        // 태그
      console.log("태그");
      for(let tag of this.tagSelected){
        if(tag instanceof Object && tag.tagNo){
          this.booktag.push({tagNo : tag.tagNo});
        }else{
          this.tmptag.push({tmptagName : tag});
        }
      }
      console.log(this.booktag);
      console.log(this.tmptag);

      // ~ 사전작업

      // 데이터 전송 ~
      
      // 사진 전송
      var form = new FormData();
      form.append("frontFile",this.front_img_file);
      form.append("middleFile",this.middle_img_file);
      form.append("backFile",this.back_img_file);

      this.parse();
      
      this.book.bookContent = this.target;
      axios.post(`/file/create_book_cover`, form
      ).then((Response)=>{
        console.log(Response.data);
        // 책 전송
        axios.post(`/book/CUD`, {
          book: this.book,
          // bcontent: this.Bcontent,
          booktag: this.booktag,
          tmptag: this.tmptag,
          fileinfo: Response.data
        }).then(()=>{
          alert("책 작성이 완료되었습니다.");
          this.$router.go(-1);
        }).catch(()=>{
          alert("책 작성을 실패했습니다.");
          // this.Bcontent = [];
          this.BookTagCat = [];
          this.booktag = [];
          this.tmptag = [];
        })
      }).catch(()=>{
        alert("이미지 업로드에 실패했습니다.");
        // this.Bcontent = [];
        this.BookTagCat = [];
        this.booktag = [];
        this.tmptag = [];
      })
      
      // ~ 데이터 전송

    },
    toggle() {
      this.isImg = !this.isImg;
      this.isContent = !this.isContent;
    },
    printtest(){
      console.log(this.book.bookIndex);
    },
    addContent(data){
      console.log(data.content);
      this.book.bookContent += data.content;
    }
  }
}
</script>


<style>
  .hideArea{
    display: none;
  }
</style>