<script>
import axios from 'axios';
import { required } from 'vuelidate/lib/validators'
export default {
  name: "BookDetail",
  validations: {
    name: { required },
  },
  methods: {
    backList(){
      this.$router.go(-1);
    },
    deleteBook(){
      axios.delete(`/book/CUD/${this.this.$route.params.bookNo}`)
      .then(() => {
        alert("책 삭제가 완료되었습니다.");
        this.$router.go(-1);
      })
      .catch(() => {
        alert("책 삭제에 실패했습니다.");
      })
    },
    favoriteClick(){
      console.log("click!!!");
    }
  },
  data(){
    return {
      writerInfo: [{}],
      book: {},
      // bcontent: [{}, {}],
      booktag: [],
      tmptag: [],
      comment: [],
      likeCount: '',
    }
  },
  created(){
    console.log("here");
    console.log(this.$route.params.bookNo);

    axios.get(`/book/book/${this.$route.params.bookNo}`)
    .then((Response) => {
      console.log("Book 조회");
      console.log(Response.data);
      console.log(Response.data.writer);
      this.book = Response.data;

      axios.post(`/user/list`,{
        searchKey : "userNo",
        searchValue : Response.data.writer
      }).then((Response)=>{
        console.log("저자 조회");
        console.log(Response.data);
        this.writerInfo = Response.data;
      })
      
    })
    .catch(()=>{
      alert("책 정보를 불러오는 데 실패했습니다.");
    })

    // axios.get(`book/bcontents?bookNo=${this.$route.params.bookNo}`)
    // .then((Response)=>{
    //   console.log("Bcontent 조회");
    //   console.log(Response.data);
    //   this.bcontent = Response.data;
    //   console.log(this.bcontent);
    // })

    axios.get(`comment/comments?bookNo=${this.$route.params.bookNo}`)
    .then((Response)=>{
      console.log("댓글 조회");
      console.log(Response.data);
      this.comment = Response.data;
    })
    .catch(()=>{
      alert("댓글 정보를 불러오는 데 실패했습니다.");
    })

    axios.get(`tag/booktags?bookNo=${this.$route.params.bookNo}`)
    .then((Response)=>{
      console.log("책태그 조회");
      console.log(Response.data);
      this.booktag = Response.data;
    })
    .catch(()=>{
      alert("책 태그 정보를 불러오는 데 실패했습니다.");
    })
    
    axios.get(`tag/tmptags?bookNo=${this.$route.params.bookNo}`)
    .then((Response)=>{
      console.log("임시태그 조회");
      console.log(Response.data);
      this.tmptag = Response.data;
    })
    .catch(()=>{
      alert("임시 태그 정보를 불러오는 데 실패했습니다.");
    })

    axios.get(`book/booklikescount?bookNo=${this.$route.params.bookNo}`)
    .then((Response)=>{
      console.log("책 좋아요 갯수 조회");
      this.likeCount = Response.data;
    })
    .catch(()=>{
      alert("책 좋아요 정보를 불러오는데 실패했습니다.");
    })
  },
  updated(){
    var pages = document.getElementsByClassName('page');
    console.log('dddddddddddddddd')
    console.log(pages.length);

    for(let i = 0; i < pages.length; i++) {
      var page = pages[i];
      if (i % 2 === 0){
        page.style.zIndex = (pages.length - i);
      }
    }

    console.log('event======================================asdasdasdasds')
    
      for(let i = 0; i < pages.length; i++){
        //Or var page = pages[i];
        pages[i].pageNum = i + 1;
        pages[i].onclick=function(){
          
          if (this.pageNum % 2 === 0){
            this.classList.remove('flipped');
            this.previousElementSibling.classList.remove('flipped');
          } 
          else{
            this.classList.add('flipped');
            this.nextElementSibling.classList.add('flipped');
          }
        }
      }
      window.Prism.highlightAll();
  },
  computed : {
    serverURL(){
      return this.$store.getters.serverURL;
    },
    userInfo() {
      return this.$store.getters.userInfo;
    },
  },
}
</script>

<style>
.book {
  transition: opacity 0.4s 0.2s;
}
.page {
  width: 30vw;
  height: 44vw;
  background-color: #111111;
  float: left;
  margin-bottom: 0.5em;
  background: left top no-repeat;
  background-size: cover;
}
.page:nth-child(even) {
  clear: both;
}
.book {
  perspective: 250vw;
}
.book .pages {
  width: 60vw;
  height: 44vw;
  position: relative;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  border-radius: 4px;
  /*box-shadow: 0 0 0 1px #e3dfd8;*/
}
.book .page {
  float: none;
  clear: none;
  margin: 0;
  position: absolute;
  top: 0;
  width: 30vw;
  height: 44vw;
  transform-origin: 0 0;
  transition: transform 1.4s;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  cursor: pointer;
  user-select: none;
  background-color: #f0f0f0;
}
.book .page:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0);
  transition: background 0.7s;
  /* z-index: 2; */
}
.book .page:nth-child(odd) {
  pointer-events: all;
  transform: rotateY(0deg);
  right: 0;
  border-radius: 0 4px 4px 0;
  background-image: linear-gradient(to right, rgba(0,0,0,.15) 0%, rgba(0,0,0,0) 10%);
}
.book .page:nth-child(odd):hover {
  transform: rotateY(-15deg);
}
.book .page:nth-child(odd):hover:before {
  background: rgba(0, 0, 0, 0.03);
}
.book .page:nth-child(odd):before {
  background: rgba(0, 0, 0, 0);
}
.book .page:nth-child(even) {
  pointer-events: none;
  transform: rotateY(180deg);
  transform-origin: 100% 0;
  left: 0;
  border-radius: 4px 0 0 4px;
  border-color: black;
  background-image: linear-gradient(to left, rgba(0,0,0,.12) 0%, rgba(0,0,0,0) 10%);
}
.book .page:nth-child(even):before {
  background: rgba(0, 0, 0, 0.2);
}
.book .page.grabbing {
  transition: none;
}
.book .page.flipped:nth-child(odd) {
  pointer-events: none;
  transform: rotateY(-180deg);
}
.book .page.flipped:nth-child(odd):before {
  background: rgba(0, 0, 0, 0.2);
}
.book .page.flipped:nth-child(even) {
  pointer-events: all;
  transform: rotateY(0deg);
}
.book .page.flipped:nth-child(even):hover {
  transform: rotateY(15deg);
}
.book .page.flipped:nth-child(even):hover:before {
  background: rgba(0, 0, 0, 0.03);
}
.book .page.flipped:nth-child(even):before {
  background: rgba(0, 0, 0, 0);
}
*,
* :before,
*:after {
  box-sizing: border-box;
}
.page:nth-child(odd){
  background-position: right top;
}
</style>