<template>
  <div class="mycomments container col order">
    <div class="row">
      <!-- data가 들어갈 부분 -->
      <div class="col" id="main">
        <v-card :elevation="0" class="col-11" id="mycomments_card">
          <v-container fluid>
            <v-row align="center">
              <v-col class="d-flex" cols="2" sm="2">
                <v-select
                  :items="items1"
                  label="검색"
                ></v-select>
              </v-col>
              <v-col md="4" class="ml-auto">
                <v-text-field v-model="search" clearable flat solo-inverted hide-details label="Search"></v-text-field>
              </v-col>
              <v-btn icon ><v-icon>mdi-magnify</v-icon></v-btn>
            </v-row>
          </v-container>

          <!-- 게시판 -->
          <b-tabs content-class="mt-3">
            <b-tab title="게시판" active>
              <table>
                <colgroup>
                  <col width="20%">
                  <col width="30%">
                  <col width="45%">
                  <col width="5%">
                </colgroup>
                <tr>
                  <th>블로그</th>
                  <th>게시글 제목</th>
                  <th>나의 댓글</th>
                  <th>채택</th>
                </tr>
                <tr v-for="comment of commentList" :key="comment.commentNo">
                  <td >{{comment.blogName}}</td>
                  <td>{{comment.articleTitle}}</td>
                  <td v-html="comment.content"></td>
                  <td v-if="comment.isPick == 1"><v-icon color="green lighten-2">mdi-checkbox-marked-circle</v-icon></td>
                </tr>
              </table>
            </b-tab>
            <b-tab title="작품">
              <table>
                <colgroup>
                  <col width="20%">
                  <col width="30%">
                  <col width="45%">
                  <col width="5%">
                </colgroup>
                <tr>
                  <th>블로그</th>
                  <th>게시글 제목</th>
                  <th>나의 댓글</th>
                  <th>채택</th>
                </tr>
              </table>
            </b-tab>
          </b-tabs>

        <!-- 페이지네이션 -->
          <v-col cols="12">
            <v-container id="page" class="max-width">
              <v-pagination
                v-model="page"
                :circle="circle"
                :disabled="disabled"
                :length="10"
                :next-icon="nextIcon"
                :prev-icon="prevIcon"
                :page="page"
                :total-visible="totalVisible"
              ></v-pagination>
            </v-container>
          </v-col>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "mycomments",
  data() {
      return {
        items1: ['전체선택', '알고리즘', '웹'],
        commentList:[],
        comment_list: [
          {
            title: [],
            date: [],
            pick: [],
            comments: [],
          },
        ],  
        selected : '1',
        // pagination 이동하게 만들어야함
        rows: 100,
        page: 1,
    }   
  },
  methods:{
    removeIframe(){
        setTimeout(() => {
          if(document.getElementsByTagName('iframe')){
            let iframes = document.getElementsByTagName('iframe');
            let len = document.getElementsByTagName('iframe').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              iframes[0].parentElement.removeChild(iframes[0]);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeIframe, 100);
          }
        }, 100)
      },
      removeCode(){
        setTimeout(() => {
          if(document.getElementsByTagName('code')){
            let codes = document.getElementsByTagName('code');
            let len = document.getElementsByTagName('code').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              codes[0].parentElement.parentElement.removeChild(codes[0].parentElement);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeCode, 100);
          }
        }, 100)
      }
  },
  components: {
    
  },

  created(){
    axios.get('/comment/mycomments')
    .then(({data}) => {
      this.commentList = data;
    })
    .catch(() => {
      alert('댓글 목록을 가져오던 중 문제가 발생했어용')
    })
  },
  updated(){
    this.removeIframe();
    this.removeCode();
  }
};
</script>

<style scoped>
#mycomments_card {
  padding: 0 30px;
}
</style>
