      },
      tab_book() {
          this.$router.push("/mainbook");
      },
       tab_blog() {
          this.$router.push("/mainblog");
      },
      tab_user() {
        this.$router.push("/mainuser");
          // this.$router.push({ name: 'Mainuser', params: { searchValue: this.$route.params.searchValue, page:this.$route.params.page ,key: this.search+this.page }});
      },
      toArticle(){
      // this.$router.push('bloghome/reviewGO/articledetail/49')
      this.$router.push('bloghome/reviewGO/articledetail/49')
      },
      searchdata(){
        if(this.search==="" || this.search ==null){
          alert("검색어를 입력하세요.");
          return;
        }
        if(this.value==="게시판"){
          alert("게시판");
        }else if(this.value==="작품"){
          alert("작품");
        }else if(this.value==="블로그"){
          this.$router.push({ name: 'Mainblog', params: { searchValue: this.search, page:this.page ,key: this.search+this.page }});
        }else if(this.value==="유저"){
          this.$router.push({ name: 'Mainuser', params: { searchValue: this.search, page:this.page ,key: this.search+this.page }});
        }
      },
      getboards(){
        axios.get('/article/articles')
        .then((Response)=>{
          console.log(Response);
          console.log("드러옴");
          for(let i = 0; i < 5; i++){
            this.boards.splice(i,1,Response.data[i]);
          }
        })
      }
  }
};
