<template>
  <div class="article-list" style="width:1050px;">
    <blog-article :boardName="boardName" @call-search="searchArticle"/>
    <div>
      <!-- for문 반복 -->
      <div class="article-contents" style="width:100%;">
        <template v-if="articleTagCatList.length > 0">
          <div v-for="articleTagCat in articleTagCatList" :key="articleTagCat.articleNo">
            <v-row class="article-post" style="height:150px;">
              <div class="article-content pl-3 d-block" style="width:1050px;">
                <p align="left" class="font-weight-medium caption mb-0" v-if="articleTagCat.article.categoryName">[{{articleTagCat.article.categoryName}}]</p>
                <p align="left" class="font-weight-medium caption mb-0" v-if="articleTagCat.article.acategoryNumber != 0">[<b>{{articleTagCat.article.acategorySource}}</b> {{articleTagCat.article.acategoryNumber}} - {{articleTagCat.article.acategoryTitle}}] </p>
                <h5 align="left" class="font-weight-black">
                  <!-- 일단 ArticleDetail.vue에 코드 잇어욥! -->
                  <router-link :to="`/bloghome/${$route.params.userId}/articledetail/${articleTagCat.article.articleNo}`" class="text-decoration-none text-dark">
                    {{articleTagCat.article.title}}
                  </router-link>
                </h5>
                <div
                  style="width:1050px; 
                    display:-webkit-box;
                    -webkit-line-clamp:3;
                    -webkit-box-orient:vertical;
                    line-height:20px;
                    height:70px;
                    justify-content:space-between; 
                    overflow:hidden; 
                    white-space:normal;
                    text-overflow:ellipsis;
                    word-break:break-all;
                    table-layout:fixed;"
                >
                  <p style="" align="left" class="font-weight-light body-1" v-html="articleTagCat.article.content">
                  </p>
                </div>  
                <v-row class="article-bottom" style="justify-content:space-between">
                  <v-col class="article-info">
                    <p align="left" class="font-weight-light caption mb-0">
                      {{articleTagCat.article.writeDate}} · 댓글수 {{articleTagCat.article.likeCount}}
                      <v-icon small>mdi-comment-text-outline</v-icon>
                    </p>
                    <p align="left" class="font-weight-medium caption mb-0">
                      <span v-for="tag in articleTagCat.arttag" :key="tag.tagNo">
                        #{{tag.tagName}}
                      </span>
                      <span v-for="tag in articleTagCat.tmpTag" :key="tag.tmptagName">
                        #{{tag.tmptagName}}
                      </span>
                    </p>
                  </v-col>
                  <v-col class="article-icon" cols="2">
                    <v-btn icon color="grey" class="mx-0 px-0 mt-2">
                      <v-icon>mdi-star-outline</v-icon>
                    </v-btn>
                    <v-btn icon color="grey" class="mx-0 px-0 mt-2">
                      <v-icon>mdi-dots-horizontal</v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </div>
              <v-col>
                <!-- <v-img src="https://picsum.photos/200/200?random=2"></v-img> -->
              </v-col>
            </v-row>
            <br>
            <hr/>
          </div>
        </template>
        <v-col cols="12">
          <v-container class="max-width">
              <!-- :circle="circle"
              :disabled="disabled" -->
              <!-- :next-icon="nextIcon"
              :prev-icon="prevIcon"
              :total-visible="totalVisible" -->
            <v-pagination
              v-model="page"
              :length="5"
              :page="page"
            ></v-pagination>
          </v-container>
        </v-col>
      </div>
    </div>
  </div>
</template>

<script>
import BlogArticle from "./BlogArticle.vue";
import axios from 'axios';
export default {
    name: 'BoardDetail',
    data(){
      return {
        articleTagCatList: [],
        boardnum : this.$route.params.boardNo,
        groupNo:'',
        seqNo:'',
        page: 1,
        boardName: '',
      }
    },
    components:{
        BlogArticle
    },
    methods:{
      searchArticle(searchValue){
          axios.get(`/article/articles?groupNo=${this.groupNo}&seqNo=${this.seqNo}&searchValue=${searchValue}`)
          .then(({data}) => {
              console.log(data);
              this.articleTagCatList = data;
              console.dir(this.articleTagCatList);
              console.log(this.articleTagCatList.length);
          })
          .catch(() => {
              alert("게시판 글 목록을 가져오는 데 실패했습니다.");
          });
      },
      removeIframe(){
        setTimeout(() => {
          if(document.getElementsByTagName('iframe')){
            let iframes = document.getElementsByTagName('iframe');
            let len = document.getElementsByTagName('iframe').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              iframes[0].parentElement.removeChild(iframes[0]);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeIframe, 100);
          }
        }, 100)
      },
      removeCode(){
        setTimeout(() => {
          if(document.getElementsByTagName('code')){
            let codes = document.getElementsByTagName('code');
            let len = document.getElementsByTagName('code').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              codes[0].parentElement.parentElement.removeChild(codes[0].parentElement);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeCode, 100);
          }
        }, 100)
      }
    },
    created(){
      if(this.boardnum){
        axios.get(`/blog/myboard?boardNo=${this.boardnum}`)
        .then(({data}) => {
          console.log(data);
          console.log(data.groupNo);
          console.log(data.seqNo);
          this.groupNo = data.groupNo;
          this.seqNo = data.seqNo;
          this.boardName = data.boardName;

          axios.get(`/article/articles?groupNo=${data.groupNo}&seqNo=${data.seqNo}`)
          .then(({data}) => {
              console.log(data);
              this.articleTagCatList = data;
              console.dir(this.articleTagCatList);
              console.log(this.articleTagCatList.length);
          })
          .catch(() => {
              alert("게시판 글 목록을 가져오는 데 실패했습니다.");
          }); 
        })
      }else{
        axios.get(`/article/articles?userId=${this.$route.params.userId}`)
        .then(({data}) => {
          
            console.log(data);
            this.articleTagCatList = data;
            console.dir(this.articleTagCatList);
            console.log(this.articleTagCatList.length);
        })
        .catch(() => {
            alert("게시판 글 목록을 가져오는 데 실패했습니다.");
        }); 
        
        }
      
    },
   
    computed: {
      userInfo() {
        return this.$store.getters.boardnumInfo;
      },
    },
    updated(){
      window.Prism.highlightAll();
      this.removeIframe();
      this.removeCode();
    }
}
</script>

