<template>
  <v-app>
    <div id="mainpage">
      <router-view :key="$route.fullPath"></router-view>
    </div>
  </v-app>
</template>

<script>
import axios from 'axios';
export default {
  name : 'Main',
  components: {
  },
  created(){
    // 192.168.100.73 우현쓰 DB 오프라인
    // 192.168.100.80 용수쓰 DB 오프라인
    // 192.168.1.6 우현쓰 DB 온라인 
    // 192.168.1.7 용수쓰 DB 온라인
    // 192.168.1.2 정훈쓰 DB 온라인
    axios.defaults.baseURL = 'http://192.168.100.80:9999'; //6이 우현이형 7이 용수

    //페이지가 새로 로딩되면 vue-session에서 jwt값을 다시 받아오기
    const jwt = this.$session.get('jwt');
    console.log(jwt);
    //axios의 default헤더로 Authorization설정후 헤더값으로 jwt설정
    if(jwt){
        axios.defaults.headers.common['Authorization'] = jwt;

        //jwt값을 디코딩 후 userId, no, type을 vuex에 다시 저장 
        axios.get('/user/mypage').then((Response)=>{
                this.$store.dispatch('Re_userInfo',Response.data)
            })
        console.dir(this.$store);
        console.dir(axios.defaults.headers);
    }
    axios.defaults.withCredentials = true;//CORS 요청시에도 클라이언트의 쿠키값 전달


  }
}
</script>

<style>
#mainpage {
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
}
.v-application code, .v-application kbd{
  all : unset !important;
  color: white !important;
}
pre>code .line-numbers-rows{
  top: 1em;
  left: 0;
}
.token.keyword{
  color: deepskyblue;
}
.token.number{
  color: greenyellow;
}
blockquote{
  padding-right: 8px !important;
  border-left-width: 5px !important;

  font-style: italic;
  font-family: Georgia, Times, "Times New Roman", serif;
  padding: 3px 20px !important;
  border-style: solid;
  border-color: #ccc !important;
  border-width: 0;
  text-align: left;

  margin-left: 20px !important;
  display: block !important;
  margin-block-start: 1em !important;
  margin-block-end: 1em !important;
  margin-inline-start: 40px !important;
  margin-inline-end: 40px !important;
}

</style>
